body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

#main {
  text-align: center;
}

#package-selector {
  display: inline-block;
  max-width: 1024px;
}
#package-name-input {
  text-align: center;
}

.query-error {
  margin-top: 16px;
  margin-bottom: 16px;
}

#query-results .query-results-cards {
  display: inline-block;
}
#query-results ul {
  list-style: none;
  padding: 0px;
  display: flex;
  position: relative;
  flex-wrap: wrap;
}
#query-results ul li {
  list-style-type: none;
  flex: 1 0 auto;
  width: 21%;
  padding: 12px;
}

@media (min-width: 600px) and (max-width: 800px) {
  #query-results ul li {
    list-style-type: none;
    flex: 1 0 auto;
    width: 30%;
    padding: 12px;
  }
}

@media (min-width: 400px) and (max-width: 600px) {
  #query-results ul li {
    list-style-type: none;
    flex: 1 0 auto;
    width: 42%;
    padding: 12px;
  }
}

@media (max-width: 400px) {
  #query-results ul li {
    list-style-type: none;
    flex: 1 0 auto;
    width: 80%;
    padding: 12px;
  }
}

.package-card:hover {
  background-color: aliceblue;
  cursor: pointer;
}
.package-card:hover .card-header {
  background-color: lightblue;
}
.package-card:hover .card-avatar {
  background-color: white !important;
  color: lightblue;
}

.card-avatar {
  background-color: lightblue !important;
}
.card-header {
  background-color: aliceblue;
  word-break: break-all;
}
.package-keywords {
  margin-top: 1em;
}
.package-scores {
  padding-top: 1em;
  display: flex;
  justify-content: space-evenly;
  opacity: 0.5;
}
.package-score-0 {
  background: rgb(150, 255, 255) !important;
}
.package-score-1 {
  background: rgb(150, 240, 240) !important;
}
.package-score-2 {
  background: rgb(150, 180, 180) !important;
}
.package-score-3 {
  background: rgb(150, 120, 120) !important;
}
.package-score-4 {
  background: rgb(150, 60, 60) !important;
}
.package-score-5 {
  background: rgb(150, 0, 0) !important;
}

.package-details.no-display {
  display: none;
}

.package-force-directed {
  display: inline-block;
  width: 800px;
  text-align: center;
}

@media (max-width: 800px) {
  .package-force-directed {
    width: 100%;
  }
}

#floating-menu {
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0;
}

